.box1 {
  /* box1box1box1box1box1box1box1box1box1box1box1box1box1box1box1box1box1box1box1box1box1box1box1box1box1 */
  mix-blend-mode: difference;
  filter: invert(1);

  box-sizing: border-box;
  position: fixed;
  padding: 20px 30px 0 30px;
  z-index: 10;
  left: 0;
  top: 0;
  width: 100vw;

  display: flex;
  justify-content: space-between;
  align-items: flex-start;

  .imgbox {
    position: relative;
    width: 85vw;
    height: auto;
    animation: intrologo2 .8s 0.6s ease-out forwards;
    display: block;
    @keyframes intrologo2 {
      to {
        width: min(30vw,180px);
        
      }
    }
    img {
      width: 100%;
      height: 100%;
    }
    
  }
  .loginbox {
    position: absolute;
    right: -10px;
    bottom: 0;
    opacity: 0;
    animation: introloginbag .5s 1.3s ease-out forwards ;
    @keyframes introloginbag {
      to{
        right: 30px;
    bottom: 0;
    opacity: 1;
      }
      
    }

    >a{
      text-decoration: none;
      color: #000;
    }
  }

  /* 햄버거버튼 ************************************************************8888*/
  .ham {
    transform: translateY(0%);

    width: 33px;
    height: 25px;
    /* outline: 1px solid red; */
    cursor: pointer;
    /* 손가락모양 */
    z-index: 10;
  }

  /* 햄버거 막대 span공통 */
  .ham span {
    position: absolute;
    width: 100%;
    height: 1.5px;
    background-color: #000000;
    // box-shadow: 0 0 4px #777; /* 박스그림자 */
    /* 트랜지션 */
    transition: all 0.2s ease-out;
  }

  /* 햄버거 막대 개별위치이동 */
  /* 1번막대 */
  .ham span:nth-child(1) {
    top: 0;
    transform: rotate(0deg);
  }

  /* 2번막대 */
  .ham span:nth-child(2) {
    top: 12px;

    /* 트랜지션 때문에 초기값필요!(기본값이 auto라서!) */
    transform: rotate(0deg);
  }

  /* 3번막대 */
  .ham span:nth-child(3) {
    top: 24px;
    transform: rotate(0deg);
  }

  /* .ham에 class="on"을 주면 span막대 변경하기 */

  /* 1번막대 : 중앙이동+시계방향45도회전 */
  .ham.on span:nth-child(1) {
    top: 10px;
    transform: rotate(45deg);
    box-shadow: none;
  }

  /* 2번막대 : left위치 중앙이동+width는0 */
  .ham.on span:nth-child(2) {
    width: 0%;
    opacity: 0;
  }

  /* 3번막대 : 중앙이동+반시계방향45도회전 */
  .ham.on span:nth-child(3) {
    top: 10px;
    transform: rotate(-45deg);
    box-shadow: none;
  }
  /* ********************************************************************* */
} /* box1box1box1box1box1box1box1box1box1box1box1box1box1box1box1box1box1box1box1box1box1box1box1box1box1box1 */


