        }
      }
      /////////////// 하위메뉴//////////////////////
      .smenu-box {
        overflow: hidden;

        .smenu-wrap {
          display: flex;
          width: 400vw; /* 4개의 하위 메뉴를 수평으로 배치하기 위해 400vw */
          transition: transform 0.3s ease; /* 애니메이션 */
