/* 1050이하 일떄 */
@media screen and (max-width: 1050px) {
  
  /* 중요:이거 없으면 smenu시 좌우, 상하 스크롤바 생김************************************************************** */
  html.on, html.on body{
    overflow: hidden;
  }
  html,body
  {
    // 자식요소중에  absolete 인 요소가 오버플로우히든을 적용하기위해
    // 최상위에 relative를 주고!! 부모자격을 주고 오버플로우 히든을 줌.
    position: relative;
    overflow-x: hidden;
  }
  /* ************************************************************** */


  .hideimg {
    display: none;
  }
}
